import React from 'react';
import SongList from './SongList';
import SongDetail from './SongDetail'; 
const App = () => {
    return(
        <div className="container">
            <div className="row">
            <div className=" col-md-6 align-items-center" style={{marginLeft:400}}><SongDetail /></div>
            </div>
            <div className="row">
                <div className=" col-md-6 align-items-center" style={{marginLeft: 200, marginTop: 100, border:"2px solid rgb(37, 209, 186)", padding: 20}}><SongList /></div>
            </div>
           
        </div>
    );
}

export default App;