import { combineReducers } from "redux";

const songsReducer = () => {
return [
    {title: 'Zara Suno', duration: '2:05'},
    {title: 'Churayi Janda Eh', duration: '1:05'},
    {title: 'Viah', duration: '3:05'},
    {title: 'Zindagi Di Paudi', duration: '4:05'}
];
};

const selectSongReducer =(selectedsong =null, action) => {
if(action.type ==='SELECT_SONG'){
    return action.payload;
}
else{
    return selectedsong;
}
}


export default combineReducers({
    songs: songsReducer,
    selectedsong: selectSongReducer
});